import { LazyLoadImage } from "react-lazy-load-image-component";
import { ReactComponent as MessageIcon } from "../icons/messages.svg";
import { ReactComponent as ClockIcon } from "../icons/clock.svg";
import CustomButton2 from "./CustomButton2";
import {
  useLocation,
  Link,
  useParams,
  useNavigate,
  Routes,
  Route,
  Outlet
} from "react-router-dom";
import { useEffect, useState } from "react";

const TestImage = require('../images/TEST.png')

const NavItem = ({ title, to }) => {
  const [isActive, setIsActive] = useState(false);
  const location = useLocation();

  useEffect(() => {
    setIsActive(location.pathname.endsWith(to) ? true : false);
  }, [location]);
  return (
    <Link to={to}>
      <div className="cursor-pointer relative group py-2 px-1">
        <p className={`${isActive ? "text-two" : "text-grayColor"}`}>{title}</p>
        <div
          className={`absolute left-0 h-[2px] bottom-0 bg-one transition-all duration-300 group-hover:w-full ${
            isActive ? "w-full" : "w-0"
          }`}
        />
      </div>
    </Link>
  );
};

const TrainerPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  return (
    <div className="p-2">
      <div className="flex justify-between pl-2 py-2">
        <div>
          <LazyLoadImage
            src={TestImage}
            alt="provider"
            className="w-[110px] h-[110px]"
          />
        </div>
        <div className="grow flex justify-between py-2 pl-4">
          <div className="flex flex-col justify-between">
            <div>
              <div className="text-one text-lg font-semibold">Full Name</div>
              <div className="text-one text-sm">Service type</div>
            </div>
            <div>
              <button className="text-white bg-one hover:bg-white border-2 border-one hover:text-one fill-white hover:fill-one transition-all duration-300 flex gap-2 items-center px-4 py-1 rounded-lg ">
                <MessageIcon className="w-5 h-5" />
                <p>Chat</p>
              </button>
            </div>
          </div>
          <div className="flex items-end">
            <CustomButton2
              onClick={() => navigate("/Trainers")}
              title="see More Trainers"
              className=""
            />
          </div>
        </div>
      </div>
      <div>
        <header className="bg-one text-white rounded-t-lg px-4 py-2 mt-2 font-semibold ">
          <div className="flex items-center w-fit gap-4">
            <ClockIcon className="w-6 h-6" />
            <p>Person</p>
          </div>
        </header>
        <div className="bg-white h-[400px] rounded-b-lg px-8 py-4">
          <nav className="flex  gap-1 sm:gap-2 md:gap-8 lg:gap-20 ">
            <NavItem title="Book" to="Book" />
            <NavItem title="Service" to="Service" />
            <NavItem title="Reviews" to="Reviews" />
            <NavItem title="Photo" to="Photo" />
            <NavItem title="About" to="About" />
          </nav>
          <div className=" mt-4">
            <Outlet />
          </div>
        </div>
      </div>
    </div>
  );
};

export default TrainerPage;
